body {
  margin: 0;
  padding: 0;
}

.main {
  padding: 20px;
}

.Navigation {
  display: flex;
  justify-content: flex-end;

  background-color: #fff;
  box-shadow: 0 0 10px #b8b8b8;
}

.Navigation__list {
  display: flex;
  /* align-items: center; */

  list-style: none;
  margin: 0;
  padding: 0;
}

.Navigation__list a {
  display: flex;
  justify-content: stretch;
  align-items: center;

  text-decoration: none;
  text-transform: uppercase;

  min-height: 70px;
  padding: 0 20px;
}

.active {
  color: #fff;
  background-color: #0000ff;
  border: #0000ff;
}

.TabsBlock--tabs {
  display: flex;

  list-style: none;
  padding: 0;
  margin: 0;
}

.TabsBlock--tab {
  position: relative;
  display: block;
  padding: 10px 20px;
  margin-right: -1px;
  background-color: #fff;

  color: #0000ff;
  text-decoration: none;
  border: 1px solid #0000ff;

  transition: all 0.5s;
}

.TabsBlock--tab:hover {
  z-index: 1;
  border-color: #fff;
  box-shadow: 0 0 7px #8585ff;
  transform: scale(1.05);
}

.active {
  color: #fff;
  background-color: #0000ff;
  border: 1px solid #0000ff;
}

.active:hover {
  color: #fff;
  background-color: #0000ff;
  border: 1px solid #0000ff;
}

.TabsBlock--tab .active {
  border: 1px solid #0000ff;
}

.TabsBlock--content {
  padding: 20px;
}
